.container {
  position: relative;
}

.sliderContainer {
  width: -moz-max-content;
  width: max-content;
}

.showImg {
  height: 390px;
  border-radius: 20px;
  cursor: zoom-in;
}

.slideImgs:nth-child(1) {
  margin-left: 0;
}

.slideImgs {
  z-index: 1;
  display: inline-block;
  width: 20px;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.5);
  position: relative;
  cursor: pointer;
  transition: 250ms;
  -o-object-fit: contain;
     object-fit: contain;
  bottom: 20px;
}

.barElements {
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 5;
}

.backward {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 200px;
  height: 646px;
  z-index: 12;
}
.backward svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.backward svg path {
  stroke: rgba(255, 255, 255, 0.5);
  transition: 250ms;
}
.backward:hover {
  cursor: pointer;
}
.backward:hover svg path {
  stroke: rgba(255, 255, 255, 0.8);
}

.forward {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 200px;
  height: 646px;
  z-index: 12;
}
.forward svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.forward svg path {
  stroke: rgba(255, 255, 255, 0.5);
  transition: 250ms;
}
.forward:hover {
  cursor: pointer;
}
.forward:hover svg path {
  stroke: rgba(255, 255, 255, 0.8);
}

.slideBar {
  display: flex;
  padding: 20px 0px;
}

.imgContainer {
  position: relative;
  z-index: 5;
  border-radius: 20px;
  margin-left: 0vw;
  margin-right: 0vw;
  height: 646px;
  border: 4px solid transparent;
  padding: 0;
}
.imgContainer img {
  max-width: 99vw;
  width: 100vw;
}

.showBar {
  max-width: 99vw;
  overflow-x: hidden;
  border: 0px;
}
.showBar div {
  border: 0px;
  width: 300vw;
  transition: 500ms;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  position: relative;
}

.activeI {
  background-color: rgba(255, 255, 255, 0.9);
}

.active {
  transition: 250ms;
}

.forward,
.backward {
  border-radius: 50%;
}

.bar {
  height: -moz-fit-content;
  height: fit-content;
}

